
import { Injectable } from '@angular/core';
import { environment } from '../../environment/environment';
import { Configuration, PluginConfig } from '../../analytics-bean/analytics-bean';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class EnvironmentService {
  envConfig: any = new Subject<PluginConfig>();

  // Setting Configuration on environment
  setConfigurationToEnvironment(configuration: Configuration, isPageLoadingToBeDetected: boolean) {
    environment.dataCollectionApi = configuration.dataCollectionApi;
    environment.isPageLoadingToBeDetected = isPageLoadingToBeDetected;
    environment.remoteConfigApi = configuration.remoteConfigApi;
    this.envConfig.next(environment);
    this.envConfig.complete();
  }

  getEnvObservable() {
    return this.envConfig;
  }
}
