import { ErrorHandler, Injectable, Injector } from '@angular/core';
import { AnalyticsService } from '../analytics/analytics.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';
import { EventLabels } from '../../types/event.types';
import { DataStorageService } from '../data-storage/data-storage.service';
@Injectable()
export class GlobalErrorHandler {
    eventLabels = EventLabels;
    constructor(private injector: Injector) {
    }

    trackConsoleErrors() {

        const analyticsService = this.injector.get(AnalyticsService);
        const dataStorageService = this.injector.get(DataStorageService);
        if (window.console && console.error) {
            const consoleErrorFnObject = console.error;
            const _self = this;
            console.error = function (...error: any[]) {
                const processedError = error.map(e => {
                    if (typeof (e) === 'object') {
                        return JSON.stringify(e);
                    } else {
                        return e;
                    }
                });
                // tslint:disable-next-line: max-line-length
                const analyticsBean: AnalyticsBean = analyticsService.setAnalyticsData
                    ('', {}, _self.eventLabels.CONSOLE_ERROR, '', { consoleErrors: JSON.stringify(processedError) });
                dataStorageService.appendToAnalyticsArray(analyticsBean);
                consoleErrorFnObject.call(console, error);
            };
        }
    }
}
